import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize } from 'rxjs/operators'
import { FileService } from 'src/app/shared/file.service';
import { AlertsService } from 'angular-alert-module';
import { NgxSpinnerService } from "ngx-spinner";


@Component({
  selector: 'app-file',
  templateUrl: './file.component.html',
  styleUrls: ['./file.component.scss']
})
export class FileComponent implements OnInit {

  formTemplate = new FormGroup({
    caption: new FormControl('', Validators.required),
    category: new FormControl(''),
    fileUrl: new FormControl('', Validators.required),
  });
  selectedFile: any = null;
  isSubmitted: boolean = false;
  
  categories: string[] = ["Science", "Mathematics", "Commerce", "Arts", "Technology", "Other"];
  constructor(private storage: AngularFireStorage, private fileService: FileService, private alerts : AlertsService,
    private spinner: NgxSpinnerService) { }

  ngOnInit() {
    this.resetForm();
  }

  onSubmit(formValue) {
    this.isSubmitted = true;
    if(this.formTemplate.valid) {
      this.spinner.show();
      let filename: string = this.selectedFile.name;
      let filePath = `${formValue.category}/${filename.substring(0, filename.indexOf('.'))}_${new Date().getTime()}${filename.substring(filename.indexOf('.'))}`;
      const fileRef = this.storage.ref(filePath);
      this.storage.upload(filePath, this.selectedFile).snapshotChanges().pipe(
        finalize(() => {
          fileRef.getDownloadURL().subscribe( url => {
            formValue['fileUrl'] = url;
            this.fileService.insertFileDetails(formValue);
            this.resetForm();
            this.spinner.hide();
            this.alerts.setMessage('File uploaded succesfully!','success');
          })
        })
      ).
      subscribe();
    }
  }

  get formControls() {
    return this.formTemplate['controls'];
  }

  onUpload(event) {
    console.log(event)
    this.selectedFile = event.target.files[0];
    
  }

  resetForm() {
    this.formTemplate.reset();
    this.formTemplate.setValue({
      caption: '',
      category: 'Science',
      fileUrl: ''
    });
    this.selectedFile = null;
    this.isSubmitted = false;
  }

}
