import { Component, OnInit, Input } from '@angular/core';
import { FileService } from 'src/app/shared/file.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-file-list',
  templateUrl: './file-list.component.html',
  styleUrls: ['./file-list.component.scss']
})
export class FileListComponent implements OnInit {

  fileList: any[];
  selectedOption: string = null;
  category: string[] = ["All","Science", "Mathematics", "Commerce", "Arts", "Technology", "Other"];

  constructor(private fileService: FileService, private router: Router) { }

  ngOnInit() {
    
    this.fileService.fileDetailList.snapshotChanges().subscribe(
      list => {
        this.fileList = list.map(item => { return item.payload.val(); })}
    )
  }

  change(evt) {
    this.fileService.fileDetailList.snapshotChanges().subscribe(
      list => {
        this.fileList = list.map(item => { 
          return item.payload.val(); 
        })
        let val: string= this.fileList[3].fileUrl;
        console.log(val.substring(val.indexOf('?')-5,val.indexOf('?')))
        console.log(this.fileList[0])
        if (evt.value !== 'All') {
          this.fileList = this.fileList.filter(s => s.category === evt.value)
        }
        
        console.log(this.fileList.filter(s => s.category === evt.value))

        
      }
    ); 
      console.log(evt.value)     
  }

  onGoback() {
    this.router.navigateByUrl('file/upload');
  }

}
